.invi {
  visibility: hidden;
  position: absolute; }

nav {
  background-color: #114411ad; }

.logo-yoga {
  background-image: url(/src/images/logo/yoga.svg);
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 5px;
  margin: 5px;
  border-radius: 50%;
  background-color: white; }

nav .brand-logo.center {
  width: 50px; }

.material-icons:hover {
  transform: scale(1.1);
  transition: 0.5s; }

.Link {
  font-size: 1.5rem; }

.Link:hover {
  background-color: rgba(0, 0, 0, 0.56); }

.sidenav {
  background-color: #adffae; }

.sidenav li > a {
  font-weight: bold; }

.sidenav li > a:hover {
  background-color: rgba(0, 0, 0, 0.27); }

.page-footer {
  background-color: #114411ad; }

section {
  min-width: 320px;
  width: 100%;
  height: auto; }

.title-principal {
  background-color: white;
  background-image: url("https://static.vecteezy.com/system/resources/previews/001/990/140/non_2x/seamless-watercolor-fresh-green-background-with-text-space-illustration-environmentally-conscious-image-with-plants-and-sunlight-horizontally-repeatable-free-vector.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  min-width: 320px;
  max-width: 350px;
  height: auto;
  margin: 100px auto;
  clip-path: polygon(2% 50%, 0 0, 0 0, 100% 0, 100% 0, 98% 50%, 100% 100%, 100% 100%, 85% 100%, 15% 100%, 0 100%, 0 100%);
  text-align: center; }

.list {
  display: flex;
  max-width: 521px;
  min-width: 320px;
  height: auto;
  margin: 0 auto;
  justify-content: center;
  padding-bottom: 100px;
  flex-direction: column; }

.filter {
  backdrop-filter: blur(2px); }

.card .card-image {
  height: 354px; }

img {
  object-fit: cover; }

.carousel .carousel-item {
  width: 223px;
  height: 347px; }

.carousel {
  text-align: center;
  transform: translateY(-84px); }

.carousel .carousel-item > img {
  width: 221px;
  height: 360px; }

.course {
  clip-path: polygon(5% 50%, 0 0, 0 0, 100% 0, 100% 0, 95% 50%, 100% 100%, 100% 100%, 85% 100%, 15% 100%, 0 100%, 0 100%);
  cursor: pointer; }

form {
  background-color: white;
  border-radius: 6px;
  padding: 10px;
  display: flex;
  max-width: 521px;
  min-width: 320px;
  height: auto;
  margin: 0 auto;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 100px; }

.input-field.col label {
  left: 0; }

.input-field > label {
  color: black; }

body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("https://images.pexels.com/photos/15286/pexels-photo.jpg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; }

h1,
h2,
h3,
p {
  margin: 0;
  padding: 5px; }

h1,
h2,
h3 {
  font-weight: bold; }

h4 {
  font-weight: 500;
  color: white; }

a {
  color: #008b04; }

label {
  color: black; }

[type="checkbox"] + span:not(.lever) {
  line-height: 25px;
  font-size: 1.1rem;
  height: 41px; }

.checkbox {
  padding-bottom: 10px; }

[type="checkbox"]:checked + span:not(.lever):before {
  border-right: 2px solid #00bd23;
  border-bottom: 2px solid #0ddb09; }

.btn:hover,
.btn-large:hover,
.btn-small:hover {
  background-color: #077c08; }

.btn,
.btn-large,
.btn-small {
  background-color: #0e3400; }

.dropdown-content li > a,
.dropdown-content li > span {
  color: #00bd23; }
